.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.appology-wrap {
		position: relative;
		padding: 2rem;
		color: var(--color-dark);
		@include max-md {
			padding: 1rem;
		}
		.elements-wrapper-absolute {
			&::after {
				position: absolute;
				top: 2rem;
				right: 2rem;
				bottom: 2rem;
				left: 2rem;
				background-color: var(--color-primary);
				opacity: 0.7;
				content: "";
				border-radius: var(--border-radius);
				@include max-md {
					top: 1rem;
					right: 1rem;
					bottom: 1rem;
					left: 1rem;
				}
			}
			img {
				object-position: 50% 100%;
				border-radius: var(--border-radius);
			}
		}
		.appology-body {
			position: relative;
			padding: 2rem 1rem;
		}
	}
}
