.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: -3rem;
		left: 0;
		width: 240px;
		transform: translate(-75%, 0);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
			transform: translate(-65%, 0);
		}
	}
	.orn-2 {
		position: absolute;
		top: -3rem;
		right: 0;
		width: 240px;
		transform: translate(75%, 0) rotate(-10deg);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
			transform: translate(65%, 0) rotate(-10deg);
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 150%;
		transform: translate(50%, 0) scaleX(-1);
	}
	.orn-4 {
		position: absolute;
		top: 5rem;
		left: 0;
		width: 150%;
		transform: translate(-30%, 0) scaleX(-1);
	}
	.orn-5 {
		position: absolute;
		top: 0;
		left: 0;
		width: 150%;
		transform: translate(-50%, -30%);
	}
	.orn-6 {
		position: absolute;
		top: 0;
		left: 60%;
		width: 120%;
		transform: translate(-50%, -30%);
	}
	.orn-7 {
		position: absolute;
		top: 0;
		right: 0;
		width: 150%;
		transform: translate(50%, -30%) scaleX(-1);
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				padding-top: 1rem;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
				.st0 {
					fill: var(--color-secondary);
				}
				.st1 {
					fill: none;
					stroke: var(--color-secondary);
					stroke-width: 19;
					stroke-linecap: round;
					stroke-miterlimit: 10;
					transform-origin: 50% 75%;
				}
			}
			.card {
				background-color: transparent;
				border: none;
				text-align: center;
				.card-body {
					position: relative;
					padding: 0;
					h4 {
						padding: 1rem 0;
					}
					.event-name {
						position: relative;
						width: 100%;
						background: linear-gradient(to bottom, var(--color-primary) 0%, transparent 100%);
						border-radius: 300px 300px 0 0;
						padding: 5rem 1rem;
						color: var(--color-dark);
						box-shadow: var(--shadow);
						z-index: 3;
						.date {
							position: relative;
							width: 100%;
							display: flex;
							flex-direction: column;
							justify-content: center;
							align-items: center;
							h5 {
								font-family: $font-secondary;
								font-weight: 700;
								margin-bottom: 0;
								font-size: 28px;
								min-width: 30%;
							}
						}
						h4 {
							font-family: $font-secondary;
						}
						p {
							margin-bottom: 0;
							font-weight: 600;
						}
					}
					.event-description {
						position: relative;
						margin-bottom: 1rem;
					}
					.event-place {
						position: relative;
						padding: 2rem;
						border-radius: var(--border-radius);
						color: var(--color-dark);
						margin-top: -1rem;
						@include max-md {
							padding: 1rem;
						}
						.elements-wrapper-absolute {
							&::after {
								position: absolute;
								top: 2rem;
								left: 2rem;
								right: 2rem;
								bottom: 2rem;
								background-color: var(--color-primary);
								opacity: 0.7;
								border-radius: var(--border-radius);
								content: "";
								@include max-md {
									top: 1rem;
									right: 1rem;
									bottom: 1rem;
									left: 1rem;
								}
							}
							img {
								object-position: 50% 100%;
								border-radius: var(--border-radius);
							}
						}
						&-body {
							position: relative;
							padding: 2rem 1rem;
							border-radius: var(--border-radius);
							.widget-elements {
								position: absolute;
								bottom: 0;
								left: 50%;
								width: 100%;
								transform: translate(-50%, 50%);
							}
						}
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 3rem;
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
