$color-main: #ab7aaf;
$color-primary: #fee3f1;
$color-secondary: #963b62;
$color-dark: #1e1f1e;
$color-light: #fff;
$text-button: #ffffff;
$color-button-trans: #fff;
$color-logo: #e9088a;
$font-secondary: "Gallery Modern", cursive;
$font-primary: "Nunito Sans", sans-serif;
$border-radius: 16px;
$shadow: inset 6px 10px 15px
	rgba(
		$color: #fee3f1,
		$alpha: 0.4,
	);

$breakpoints-min: (
	xs: 325px,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px,
	xxl: 1440px,
	4k: 2560px,
);

$breakpoints-max: (
	xs: 375px,
	sm: 575px,
	md: 767px,
	lg: 991px,
	xl: 1199px,
	xxl: 1399px,
);
