.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 240px;
		transform: translate(-60%, -20%);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 240px;
		transform: translate(60%, -20%) rotate(-10deg);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translate(-50%, 0);
		@include min-md {
			width: 80%;
		}
		@include min-lg {
			width: 70%;
			transform: translate(-50%, 10%);
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translate(-40%, 40%);
		@include min-md {
			width: 80%;
			transform: translate(-30%, 40%);
		}
		@include min-lg {
			width: 70%;
			transform: translate(-30%, 50%);
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(20%, 10%);
		@include min-md {
			width: 80%;
			transform: translate(10%, 10%);
		}
		@include min-lg {
			width: 70%;
			transform: translate(10%, 20%);
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		right: 10%;
		width: 80%;
		transform: translate(0, 20%);
		@include min-md {
			width: 60%;
		}
		@include min-lg {
			width: 50%;
			transform: translate(0, 30%);
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 0;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 0;
			align-items: center;
		}
		.cover-content {
			position: relative;
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				padding: 3rem 0 5rem;
				width: 35%;
				height: 100%;
				color: var(--color-dark);
				@include max-xl {
					width: 40%;
				}
				@include max-lg {
					width: 65%;
				}
				@include max-md {
					width: 100%;
					padding: 3rem 0 7rem;
				}
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: linear-gradient(to bottom, var(--color-primary) 0%, transparent 100%);
					content: "";
					border-radius: var(--border-radius);
					box-shadow: inset 6px 10px 15px rgba($color: #fee3f1, $alpha: 0.4);
				}
				.cover-description {
					padding: 2rem;
					text-align: center;
					@include max-lg {
						padding: 1rem;
					}
					.cover-event {
						position: relative;
						margin-bottom: 1rem;
					}
					.greeting-wrapper {
						position: relative;
						margin-bottom: 1rem;
					}
					.title-cover {
						padding: 1rem 0;
						margin-bottom: 0;
						display: flex;
						align-items: center;
						justify-content: center;
						column-gap: 0.5rem;
						@include max-md {
							flex-direction: column;
						}
						.and {
							font-size: 32px;
							@include max-md {
								font-size: 28px;
							}
						}
					}
					p {
						margin-bottom: 0;
					}
					h6 {
						margin-bottom: 0;
						font-weight: 600;
					}
				}
			}
			.elements-widget {
				position: relative;
				width: 100%;
			}
		}
	}
}
