.thank-section {
	position: relative;
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 5/6;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.frame {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 130%;
		transform: translate(-50%, 60%);
	}
	.orn-99 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 90%;
		.image-element {
			height: 100%;
			.img-fluid {
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.orn-1 {
		position: absolute;
		bottom: -5rem;
		left: 0;
		width: 240px;
		transform: translate(-60%, 0);
		z-index: 5;
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: -5rem;
		right: 0;
		width: 240px;
		transform: translate(60%, 0) rotate(-10deg);
		z-index: 5;
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-30%, 0);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 120px;
			transform: translate(-10%, 50%) scaleX(-1);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: translate(30%, 0) scaleX(-1);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(0, 70%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120%;
		transform: translate(-40%, 45%);
		z-index: 3;
		@include min-md {
			width: 100%;
			transform: translate(-40%, 65%);
		}
		@include min-lg {
			width: 80%;
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translate(-40%, -20%);
		@include min-md {
			width: 80%;
		}
		@include min-lg {
			width: 70%;
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(20%, 60%);
		z-index: 3;
		@include min-md {
			width: 80%;
		}
		@include min-lg {
			width: 70%;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(20%, 30%);
		z-index: 3;
		@include min-md {
			width: 80%;
			transform: translate(20%, 50%);
		}
		@include min-lg {
			width: 70%;
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120%;
		transform: translate(0, -20%);
		@include min-md {
			width: 100%;
		}
		@include min-lg {
			width: 80%;
			transform: translate(0, 5%);
		}
	}
	.orn-12 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translate(-60%, 35%);
		z-index: 3;
		@include min-md {
			width: 80%;
		}
		@include min-lg {
			width: 70%;
		}
	}
	.thank-wrapper {
		position: relative;
		padding: 5rem 0 3rem;
		text-align: center;
		@include max-md {
			padding: 3rem 0 8rem;
		}
		.thank {
			position: relative;
			padding: 8rem 2rem 5rem;
			background-color: #ededed;
			border-radius: 300px 300px 10px 10px;
			color: var(--color-dark);
			@include max-md {
				padding: 5rem 1rem 3rem;
			}
			&-body {
				position: relative;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
