.rsvp-section {
	position: relative;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 50%;
		transform: translate(-20%, 0);
		z-index: 3;
		@include max-lg {
			width: 70%;
		}
		@include max-md {
			width: 80%;
			transform: translate(-40%, 0);
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 50%;
		transform: translate(-30%, 10%) scaleX(-1);
		z-index: 3;
		@include max-lg {
			width: 70%;
		}
		@include max-md {
			width: 80%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 50%;
		transform: translate(20%, 0) scaleX(-1);
		z-index: 3;
		@include max-lg {
			width: 70%;
		}
		@include max-md {
			width: 80%;
			transform: translate(40%, 0) scaleX(-1);
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120%;
		transform: translate(-30%, -20%) scaleX(-1);
		@include min-md {
			width: 80%;
		}
		@include min-lg {
			width: 50%;
			transform: translate(30%, -20%) scaleX(-1);
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120%;
		transform: translate(50%, 0) scaleX(-1);
		@include min-md {
			width: 80%;
		}
		@include min-lg {
			width: 50%;
			transform: translate(-20%, 0) scaleX(-1);
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 12%;
		left: 0;
		width: 120px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 10%;
		right: 0;
		width: 120px;
		transform: translate(50%, 0);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 2rem;
		color: var(--color-dark);
		@include max-md {
			padding: 1rem;
		}
		.elements-wrapper-absolute {
			&::after {
				position: absolute;
				top: 2rem;
				right: 2rem;
				bottom: 2rem;
				left: 2rem;
				background: linear-gradient(to bottom, var(--color-primary) 0%, transparent 100%);
				border-radius: 300px 300px 0 0;
				box-shadow: var(--shadow);
				content: "";
				@include max-md {
					top: 1rem;
					right: 1rem;
					bottom: 1rem;
					left: 1rem;
				}
			}
			.background-element {
				img {
					border-radius: 300px 300px 10px 10px;
					object-position: 50% 100%;
				}
			}
		}
		.rsvp-form {
			position: relative;
			padding: 8rem 2rem 3rem;
			z-index: 5;
			.title-section {
				&::before {
					all: unset;
				}
			}
		}
	}
}
