.reminder-wrap {
	position: relative;
	display: block;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 240px;
		transform: translate(-70%, -50%);
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
		.orn-2 {
			position: relative;
			width: 120px;
			margin-left: auto;
			transform: translate(-10%, -90%);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 60%;
		right: 0;
		width: 120px;
		transform: translate(50%, -50%);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
		.orn-4 {
			position: relative;
			width: 240px;
			transform: translate(0, -30%);
			@include min-md {
				width: 280px;
			}
			@include min-lg {
				width: 320px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 0;
		left: 38%;
		width: 100%;
		transform: translate(-50%, -20%);
		@include min-md {
			width: 60%;
		}
		@include min-lg {
			width: 30%;
			transform: translate(-30%, -20%);
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 150%;
		transform: translate(-60%, 40%);
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 140%;
		transform: translate(-50%, 40%) scaleX(-1);
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 150%;
		transform: translate(60%, 40%) scaleX(-1);
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		margin: 0 auto;
		max-width: 100%;
		padding: 5rem 2rem 2rem;
		color: var(--color-dark);
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: linear-gradient(to bottom, var(--color-primary) 0%, transparent 100%);
			border-radius: 300px 300px 10px 10px;
			box-shadow: inset 6px 10px 15px rgba($color: #fee3f1, $alpha: 0.4);
			content: "";
		}
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
