.stream-section {
	position: relative;
	padding: 5rem 0;
	background-color: var(--color-primary);
	color: var(--color-dark);
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 10%;
		left: 0;
		width: 120px;
		transform: translate(-50%, -20%) scaleX(-1);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: translate(55%, -20%) scaleX(-1);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.stream-wrapper {
		position: relative;
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 2rem;
				text-align: center;
				@include max-md {
					padding: 1rem;
				}
				.elements-wrapper-absolute {
					&::after {
						position: absolute;
						top: 2rem;
						right: 2rem;
						bottom: 2rem;
						left: 2rem;
						background-color: var(--color-primary);
						opacity: 0.7;
						content: "";
						border-radius: var(--border-radius);
						@include max-md {
							top: 1rem;
							right: 1rem;
							bottom: 1rem;
							left: 1rem;
						}
					}
					img {
						object-position: 50% 100%;
						border-radius: var(--border-radius);
						box-shadow: 0 30px 50px rgba($color: #000000, $alpha: 0.15);
					}
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		padding: 2rem 1rem;
	}
}
