.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 25rem;
		left: 0;
		width: 120px;
		transform: translate(-50%, 0);
		z-index: 3;
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			top: 70%;
			width: 180px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 25rem;
		right: 0;
		width: 120px;
		transform: translate(50%, 0);
		z-index: 3;
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			top: 70%;
			width: 180px;
		}
	}
	.story {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-dark);
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		padding: 0 1rem 0 3rem;
		@include min-lg {
			.title-section {
				text-align: left;
			}
		}
		@include max-lg {
			padding: 2rem 1rem;
			text-align: center;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 5/6;
		@include max-lg {
			aspect-ratio: 16/9;
		}
		@include max-md {
			aspect-ratio: 5/6;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
