.header-section {
	position: relative;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
		rect {
			stroke: var(--color-button-trans);
		}
	}
	.orn-99 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 50%;
		.image-element {
			height: 100%;
			.img-fluid {
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 240px;
		transform: translate(-60%, 0);
		z-index: 5;
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 240px;
		transform: translate(60%, 0) rotate(-10deg);
		z-index: 5;
		@include min-md {
			width: 280px;
		}
		@include min-lg {
			width: 320px;
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-30%, 0);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 120px;
			transform: translate(-10%, 50%) scaleX(-1);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		right: 0;
		width: 120px;
		transform: translate(30%, 0) scaleX(-1);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(0, 70%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120%;
		transform: translate(-40%, 30%);
		z-index: 3;
		@include min-md {
			width: 100%;
			transform: translate(-40%, 45%);
		}
		@include min-lg {
			width: 80%;
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translate(-40%, -20%);
		@include min-md {
			width: 80%;
		}
		@include min-lg {
			width: 70%;
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(20%, 60%);
		z-index: 3;
		@include min-md {
			width: 80%;
		}
		@include min-lg {
			width: 70%;
		}
	}
	.orn-10 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(20%, 30%);
		z-index: 3;
		@include min-md {
			width: 80%;
		}
		@include min-lg {
			width: 70%;
			transform: translate(10%, 30%);
		}
	}
	.orn-11 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120%;
		transform: translate(0, -20%);
		@include min-md {
			width: 100%;
		}
		@include min-lg {
			width: 80%;
		}
	}
	.orn-12 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		transform: translate(-60%, 35%);
		z-index: 3;
		@include min-md {
			width: 80%;
			transform: translate(-60%, 60%);
		}
		@include min-lg {
			width: 70%;
		}
	}
	.orn-13 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 120%;
		transform: translate(-40%, 70%);
		z-index: 3;
		@include min-md {
			display: none;
		}
	}
	.orn-14 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(20%, 70%);
		z-index: 3;
		@include min-md {
			display: none;
		}
	}
	.orn-15 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		transform: translate(20%, 50%);
		z-index: 3;
		@include min-md {
			display: none;
		}
	}
	.header-content {
		position: relative;
		.header {
			position: relative;
			width: 35%;
			height: 100%;
			margin: 0 auto;
			padding: 4.5rem 0 2rem;
			@include max-xl {
				width: 40%;
			}
			@include max-lg {
				width: 65%;
			}
			@include max-md {
				width: 100%;
				padding: 4.5rem 0 5rem;
			}
			.header-title {
				position: relative;
				text-align: center;
				margin-bottom: 1rem;
				&-content {
					position: relative;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 100%;
				height: auto;
				aspect-ratio: 5/6;
				@include max-md {
					width: 85%;
					margin: 0 auto;
				}
				.image-element.background-element {
					height: 100%;
					background-color: #ededed;
					padding: 1rem;
					border-radius: 300px 300px 0 0;
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 300px 300px 0 0;
					}
				}
				.frame {
					position: absolute;
					bottom: 0;
					left: 50%;
					width: 130%;
					transform: translate(-50%, 60%);
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
