.couple-section {
	position: relative;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -60%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		padding: 2rem 0 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 28px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		.orn-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 240px;
			transform: translate(-65%, 0);
			z-index: 3;
			@include min-md {
				width: 280px;
			}
			@include min-lg {
				width: 320px;
			}
		}
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 240px;
			transform: translate(65%, 0);
			z-index: 3;
			@include min-md {
				width: 280px;
			}
			@include min-lg {
				width: 320px;
			}
		}
		.orn-3 {
			position: absolute;
			top: 1rem;
			left: 0;
			width: 120px;
			transform: translate(-50%, 0);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
		.orn-4 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 140px;
			transform: translate(50%, 0);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
		.orn-5 {
			position: absolute;
			top: 10%;
			left: 0;
			width: 120px;
			transform: translate(-20%, 0);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
		.orn-6 {
			position: absolute;
			top: 40%;
			right: 0;
			width: 120px;
			transform: translate(35%, 0);
			@include min-md {
				width: 160px;
			}
			@include min-lg {
				width: 180px;
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 120%;
			transform: translate(-65%, 95%);
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			left: 35%;
			width: 120%;
			transform: translate(-50%, 95%);
		}
		.orn-9 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 120%;
			transform: translate(30%, 95%);
		}
		.orn-10 {
			position: absolute;
			top: 85%;
			left: 0;
			width: 120%;
			transform: translate(-65%, -50%);
		}
		.orn-11 {
			position: absolute;
			top: 85%;
			right: 0;
			width: 120%;
			transform: translate(30%, -50%);
		}
		.orn-12 {
			position: absolute;
			top: 0;
			right: 0;
			width: 130%;
			transform: translate(50%, 0) scaleX(-1);
		}
		.orn-13 {
			position: absolute;
			top: 0;
			left: 0;
			width: 130%;
			transform: translate(-40%, 10%) scaleX(-1);
		}
		.orn-14 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 120%;
			transform: translate(-65%, 120%);
		}
		.orn-15 {
			position: absolute;
			bottom: 0;
			left: 35%;
			width: 120%;
			transform: translate(-50%, 120%);
		}
		.orn-16 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 120%;
			transform: translate(30%, 120%);
		}
	}
	.couple-body {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
	}
	.couple-content-container {
		position: relative;
		background-color: var(--color-primary);
		padding: 5rem 0 0;
		@include max-md {
			padding: 3rem 0 0;
		}
		.couple-content {
			position: relative;
			padding: 5rem 2rem 2rem;
			border-radius: var(--border-radius);
			overflow: hidden;
			@include max-md {
				padding: 3rem 1rem 1rem;
			}
			.elements-wrapper-absolute {
				.background-element {
					img {
						object-position: 50% 100%;
					}
				}
			}
			&-body {
				position: relative;
				color: var(--color-dark);
				padding: 3rem 2rem;
				@include max-md {
					padding: 2rem 1rem;
				}
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: var(--color-primary);
					opacity: 0.7;
					content: "";
					border-radius: var(--border-radius);
				}
			}
		}
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 7/8;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				background-color: #ededed;
				border-radius: 300px 300px 0 0;
				padding: 1rem;
				.couple-image {
					height: 100%;
					object-fit: cover;
					width: 100%;
					border-radius: 300px 300px 0 0;
				}
			}
			.frame {
				position: absolute;
				bottom: 0;
				left: 50%;
				width: 130%;
				transform: translate(-50%, 50%);
			}
		}
		.couple-description {
			position: relative;
			margin-top: 8rem;
			color: var(--color-dark);
			@include max-lg {
				margin-top: 5rem;
			}
			&::before {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: var(--color-primary);
				opacity: 0.7;
				border-radius: var(--border-radius);
				content: "";
			}
			&-body {
				position: relative;
				padding: 1rem;
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 50%);
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.75rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
